import { Token } from '@lumino/coreutils';
import { ILayoutRestorer } from '@jupyterlab/application';
import { MainAreaWidget, ICommandPalette, WidgetTracker } from '@jupyterlab/apputils';
import { ISettingRegistry } from '@jupyterlab/settingregistry';
import { ILauncher } from '@jupyterlab/launcher';
import icon from '@datalayer/icons-react/data2/WhaleSpoutingIconJupyterLab';
import { requestAPI } from './handler';
import { JupyterEmbedWidget } from './widget';
import { Timer } from "../state/zustand";
import { TimerView } from "../timer/TimerView";
import { mobxTimer, MobxTimerView } from "../state/mobx";
import '../../style/index.css';
export const IJupyterEmbed = new Token('@datalayer/jupyter-embed:plugin');
/**
 * The command IDs used by the plugin.
 */
var CommandIDs;
(function (CommandIDs) {
    CommandIDs.create = 'create-jupyter-embed-widget';
})(CommandIDs || (CommandIDs = {}));
/**
 * Initialization data for the @datalayer/jupyter-embed extension.
 */
const plugin = {
    id: '@datalayer/jupyter-embed:plugin',
    autoStart: true,
    requires: [ICommandPalette],
    optional: [ISettingRegistry, ILauncher, ILayoutRestorer],
    provides: IJupyterEmbed,
    activate: (app, palette, settingRegistry, launcher, restorer) => {
        const { commands } = app;
        const command = CommandIDs.create;
        const tracker = new WidgetTracker({
            namespace: 'jupyter-embed',
        });
        if (restorer) {
            void restorer.restore(tracker, {
                command,
                name: () => 'jupyter-embed',
            });
        }
        const jupyterDocker = {
            timer: new Timer(),
            TimerView,
            mobxTimer,
            MobxTimerView,
        };
        commands.addCommand(command, {
            caption: 'Show Jupyter Embed',
            label: 'Jupyter Embed',
            icon,
            execute: () => {
                const content = new JupyterEmbedWidget(app, jupyterDocker);
                const widget = new MainAreaWidget({ content });
                widget.title.label = 'Jupyter Embed';
                widget.title.icon = icon;
                app.shell.add(widget, 'main');
                tracker.add(widget);
            }
        });
        const category = 'Datalayer';
        palette.addItem({ command, category });
        if (launcher) {
            launcher.add({
                command,
                category,
                rank: 3.1,
            });
        }
        if (settingRegistry) {
            settingRegistry
                .load(plugin.id)
                .then(settings => {
                console.log('@datalayer/jupyter-embed settings loaded:', settings.composite);
            })
                .catch(reason => {
                console.error('Failed to load settings for @datalayer/jupyter-embed.', reason);
            });
        }
        requestAPI('config')
            .then(data => {
            console.log(data);
        })
            .catch(reason => {
            console.error(`Error while accessing the jupyter server jupyter_embed extension.\n${reason}`);
        });
        console.log('JupyterLab plugin @datalayer/jupyter-embed:plugin is activated.');
        return jupyterDocker;
    }
};
export default plugin;
